declare var swal: any;
import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { errorMessage } from 'src/app/messages';
import { CompanyService } from 'src/app/service/company.service';
import { DataService } from 'src/app/service/data.service';
import { PaginationService } from 'src/app/service/pagination.service';
import { CommonService } from 'src/app/service/common.service';
import { ISubscription } from "rxjs/Subscription";
@Component({
  selector: 'app-company-list',
  templateUrl: './company-list.component.html'
})
export class CompanyListComponent implements OnInit {

  loadingState = true;
  objectArray: Array<any> = [];
  pagination: any = null;
  searchText: any = null;
  currentPage: any = 1;
  private subscription: ISubscription;
  constructor(
    private router: Router,
    private companyService: CompanyService,
    private commonService:CommonService,
    private dataService: DataService,
    private paginationService:PaginationService
  ) {
   }

   ngOnInit() {
    this.subscription= this.dataService.searchTextSubject.subscribe((v) =>{ 
      this.searchText=v;
      this.getObjects();
    });
  }


  getObjects() {
    this.loadingState = true;
    const params: any = { page: this.currentPage};
    if (this.searchText) {
      params.search = this.searchText;
    }
    this.companyService.getCompanyList(params).subscribe((response) => {
      // console.log(response);
      this.loadingState = false;
      if (response.success) {
        this.objectArray = response.data.data;
        this.pagination = this.paginationService.getPager(response.data.pagination['total_page'], this.currentPage);
      } else {
        this.objectArray = [];
        this.pagination = null;
      }
    }, (error) => {
      this.loadingState = false;
      this.objectArray = [];
      this.pagination = null;
      // console.log(error);
    });
  }

  getPage(page: number) {
    this.currentPage = page;
    this.getObjects();
  }

  searchObject(text) {
    this.searchText = text;
    this.getObjects();
  }

  deleteObject(object) {
    swal.fire({
      title: errorMessage.delete_header_text,
      text: errorMessage.delete_smalll_text,
      icon: errorMessage.delete_dialogue_type,
      showCancelButton: true,
      confirmButtonText: errorMessage.delete_confirm_button,
      cancelButtonText: errorMessage.delete_cancel_button,
    }).then((result) => {
      if (result.value) {
        this.companyService.deleteCompany(object.id).subscribe(
          (response) => {
            if (response.success) {
              this.getObjects();
            }
          },
        );
      }
    }).catch(swal.noop);
  }

  onCompanySelect(obj) {
    // console.log(obj);
    this.commonService.selectCompany(obj.id).subscribe((response) => {
      if (response.success) {
        // this.dataService.saveToken(response.data.token);
        this.dataService.updateCompany(response.data.company);
        this.router.navigateByUrl('/dashboard');
      } else {
        this.objectArray = [];
        this.pagination = null;
      }
    });
  }

  ngOnDestroy() {
    if (this.subscription) {
      this.subscription.unsubscribe();
    }
  }
  
  
}
