import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { Routes, RouterModule } from '@angular/router';

import { CompanyListComponent } from './company-list.component';
import { CompanyService } from 'src/app/service/company.service';
import { PaginationModule } from 'src/app/common/pagination/pagination.module';
import { LoderModule } from 'src/app/common/loder/loder.module';


const routes: Routes = [
  {
    path: '',
    component: CompanyListComponent,
    data: { title: 'company_list' }
  },
  {
    path: 'add',
    loadChildren: () => import('../add-company/add-company.module').then(m => m.AddCompanyModule),
    data: { title: 'add_company' }
  },
  {
    path: 'edit/:id',
    loadChildren: () => import('../add-company/add-company.module').then(m => m.AddCompanyModule),
    data: { title: 'edit_company' }
  },
];

@NgModule({
  declarations: [
    CompanyListComponent
  ],
  imports: [
    CommonModule,
    PaginationModule,
    LoderModule,
    RouterModule.forChild(routes)
  ],
  providers: [
    CompanyService
  ]
})
export class CompanyListModule { }
