import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';


@Injectable()
export class CompanyService {

  API_URL: string = environment.APIEndpoint;

  constructor(
    private http: HttpClient,
  ) { }

  getCompanyList(param): Observable<any> {
    return this.http.get(`${this.API_URL}/company`, {params: param});
  }

  addCompany(data): Observable<any> {
    return this.http.post(`${this.API_URL}/company`, data);
  }

  editCompany(id, data): Observable<any> {
    return this.http.put(`${this.API_URL}/company/${id}`, data);
  }

  deleteCompany(id): Observable <any> {
    return this.http.delete(`${this.API_URL}/company/${id}`);
  }

  getCompanyById(id, param = null): Observable<any> {
    return this.http.get(`${this.API_URL}/company/${id}`, {params: param});
  }

 

 

}
